import { Axios } from 'axios'
import {
  Operator,
  OperatorRoutes,
  OperatorsClientModel,
  OperatorsSearchQuery,
} from '../operator.model'
import { ApiEntity } from '../../core'

export class OperatorsClient extends ApiEntity implements OperatorsClientModel {
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search operators by params
   * @param searchParams - the filters to apply
   * @returns an operators page
   */
  public search(searchParams?: OperatorsSearchQuery) {
    return this._search<Operator>(OperatorRoutes.v1Search, searchParams)
  }

  /**
   * List operators by params
   * @param searchParams - the filters to apply
   * @returns the operators list
   */
  public list(searchParams?: OperatorsSearchQuery) {
    return this._list<Operator>(OperatorRoutes.v1Search, searchParams)
  }

  /**
   * Find an operator by params
   * @param searchParams - the filters to apply
   * @returns an optional operator
   */
  public findOne(searchParams?: OperatorsSearchQuery) {
    return this._findOne<Operator>(OperatorRoutes.v1Search, searchParams)
  }
}
